import React, { Component } from 'react';
import {
  Card,
  CardActionArea,
  CardMedia,
  CardContent,
  Typography,
  Button,
  CardActions,
  Grid,
  TextField,
} from '@material-ui/core';
import { getProjects } from './Data';
import Autocomplete from '@material-ui/lab/Autocomplete';

class Projects extends Component {
  constructor(props) {
    super(props);
    var categories = [];
    getProjects().map((project) => {
      project.hashtags.map((tag) => {
        if (!categories.includes(tag)) {
          categories.push(tag);
        }
      });
    });

    this.state = {
      categories: categories.sort(this.sortFn),
      filterOption: null,
      projects: getProjects(),
    };
  }

  // displayHashtags(tags) {
  //   return (
  //     <div>
  //       {tags.map(tag => {

  //       }}
  //     </div>
  //   )
  // }

  sortFn(a, b) {
    if (a < b) {
      return -1;
    } else {
      return 1;
    }
  }

  refreshList(value) {
    var newProjects = getProjects();
    if (value !== null) {
      newProjects = getProjects().filter((project) => {
        return project.hashtags.includes(value);
      });
    }
    this.setState({
      projects: newProjects,
    });
  }

  render() {
    return (
      <div
        style={{
          height: '100%',
          width: '90vw',
          margin: 'auto',
          padding: '20px',
        }}
      >
        <Autocomplete
          value={this.state.filterOption}
          options={this.state.categories}
          renderInput={(params) => <TextField {...params} label="Filter" variant="outlined" />}
          onChange={(event, value) => {
            this.setState({ filterOption: value });
            this.refreshList(value);
          }}
          style={{ paddingBottom: '20px' }}
        />
        <Grid
          container
          direction="row"
          spacing={3}
          style={{ flexGrow: 1 }}
          justify="center"
          alignItems="center"
        >
          {this.state.projects.map((value) => (
            <Grid item xs={12} sm={6} md={4}>
              <Card>
                <CardMedia
                  image={value.placeholder != '' ? require(`${value.placeholder}`) : ''}
                  style={{
                    height: value.placeholder != '' ? '100px' : 0,
                    paddingTop: value.placeholder != '' ? '70%' : 0,
                    margin: 'auto',
                  }}
                ></CardMedia>
                <CardContent>
                  <Typography variant="h5" component="h2">
                    {value.title}
                  </Typography>
                  <Typography variant="body2" component="p">
                    {value.description}
                  </Typography>
                </CardContent>
                <CardActions>
                  <Grid container direction="column">
                    <Grid item>
                      {value.buttons.map((button) => (
                        <Button href={button.href} target="_blank" rel="noopener" size="small">
                          {button.title}
                        </Button>
                      ))}
                    </Grid>
                    <Grid
                      item
                      container
                      direction="row"
                      justify="flex-start"
                      style={{ padding: '5px' }}
                      spacing={1}
                    >
                      {value.hashtags.map((tag) => (
                        <Grid item>
                          <Typography
                            variant="body1"
                            style={{ fontSize: '0.5rem', cursor: 'pointer' }}
                            onClick={() => {
                              this.setState({ filterOption: tag });
                              this.refreshList(tag);
                            }}
                          >
                            {tag}
                          </Typography>
                        </Grid>
                      ))}
                    </Grid>
                  </Grid>
                </CardActions>
              </Card>
            </Grid>
          ))}
        </Grid>
      </div>
    );
  }
}

export default Projects;
